import {React,useEffect,useState} from 'react'
import Container from 'react-bootstrap/Container';
import logooo from '../../Asset/amazonlogo.png'
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Badge from '@mui/material/Badge';
import { NavLink } from 'react-router-dom';
import Menu from '@mui/material/Menu';
import cart from '../../Asset/cart.gif'
import '../../components/CardHeader/CardStyle.css'
import { useDispatch, useSelector } from 'react-redux';
import { Table } from 'react-bootstrap';
import { DLT } from '../../redux/Actions/Action';


const CardHeaderOne = () => {



  const getdata = useSelector((state)=> state.cartreducer.carts);

const dispatch =useDispatch();
const dlt= (id)=>{
  dispatch(DLT(id))
}



    const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };


  const [price,setPrice]= useState(0);

  const total = ()=>{
    let price= 0;
    getdata.map((ele,k)=>{
      price = ele.price + price
    });
    setPrice(price);
  }

  useEffect(()=>{
    total();
  },[total] )



  return (
    <>
        <Navbar collapseOnSelect expand="lg" fixed="top" className='navBaar' variant="light">
        <Container fluid >
        <Navbar.Brand to="/">
            <img
              alt=""
              src={logooo}
              width="30"
              height="30"
              className="d-inline-block align-top"
            />{' '}
            <NavLink to='/Amazon_Ecom' style={{fontSize:'2rem',textDecoration:'none',letterSpacing:'1px',paddingLeft:'2px'}}>Amazon</NavLink>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
          <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto text-light">
        
          
          
            
          <NavLink className='navbtn' to="/Amazon_Ecom">Home</NavLink>
          <NavLink className='navbtn' to='/Collections'>Collections</NavLink>
            
          
        
            
          </Nav>
          </Navbar.Collapse>
          
          <NavLink className='btn2' to="/SignUp">SIGN UP</NavLink>
          <Badge className='navContainer' badgeContent={getdata.length} color="primary"
          
          id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
        >
           <Navbar.Text>ADD TO CART <i style={{cursor:"pointer"}} className='fa-solid fa-cart-shopping'></i></Navbar.Text>
          </Badge>

          
        </Container>

        <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {
          getdata.length ?
          <div className='card_details' style={{width:'24rem', padding:10}}>
            <Table>
              <thead>
                <tr>
                  <th>Photo</th>
                  <th>Products Name</th>
                </tr>
              </thead>
              <tbody>
                {
                  getdata.map((e)=>{ 
                    return(
                      <>
                      <tr>
                        <td>
                       <NavLink to={`/CartDetails/${e.id}`} onClick={handleClose}>
                       <img src={e.imgdata} alt="" srcset="" style={{width:'5rem', height:'5rem', padding:2}} />
                       </NavLink>   
                        </td>
                        <td>
                          <p>{e.rname}</p>
                          <p>Price : {e.price}</p>
                          <p>Quantity : {e.qnty}</p>
                          <p style={{color:"red", fontSize:20, cursor:"pointer"}} onClick={()=>dlt(e.id)}>
                            <i className='fas fa-trash smalltrash'></i>
                          </p>
                        </td>
                        <td className='mt-5' style={{color:"red", fontSize:20, cursor:"pointer"}} onClick={()=>dlt(e.id)} >
                        <i className='fas fa-trash largetrash'></i>
                        </td>
                      </tr>
                      </>
                    )
                  })
                }
                <p className='text-center'> Total : {price}</p>
              </tbody>
            </Table> 
            </div> :
            <div className='card_details d-flex justify-content-center align-items-center' style={{width:"24rem",padding:10,position:"relative"}}>
                    <i className='fas fa-close smallclose'
                    onClick={handleClose}
                     style={{position:"absolute",top:2,right:20,fontSize:23,cursor:"pointer"}}></i>
                    <p style={{fontSize:22}}>Your carts is empty</p>
                    <img src={cart} alt="Cart" className='emptycart_img' style={{width:"5rem",padding:10}} />
                   </div>

          
        }
       
      </Menu>
      </Navbar>
    
    </>
  )
}

export default CardHeaderOne